/* About Page design */

.about_page {
    h2 {
        padding: 0px;
    }

    .bg_img h1 {
        font-weight: 700;
    }
}

.about_page .section-title {
    margin-top: 70px;
    margin-bottom: 46px;
}
.about_page {
    .section-tumbnail {
        margin-bottom: 30px;

        img {
            max-width: 405px;
            margin-bottom: 0px;
            width: 100%;
        }
    }

    .section-body p {
        font-size: 16px;
        line-height: 28px;
    }
}

.section-community .section-body p {
    margin-bottom: 76px;
    text-align: justify;
}

.section.online-home.jobs {
    padding: 30px 0px 50px;

    p {
        max-width: 991px;
        margin: 0 auto;
        font-family: 'Open Sans', sans-serif;
    }

    .section-title {
        margin-bottom: 15px;
        margin-top: 0px;
        font-weight: normal;
        font-size: 29px;
        line-height: 38px;
        color: #fff;
    }
}
.virtual_workers .text-content p {
    font-weight: normal;
    font-size: 16px;
    line-height: 36px;
    text-align: justify;
    margin-bottom: 0px;
}
.section .funnel {
    margin: 0px;
    padding: 63px 35px 35px;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2);
    margin-bottom: 81px;
    margin-top: 10px;

    h2 {
        margin-top: 0px;
        margin-bottom: 20px;
        font-weight: normal;
        font-size: 36px;
        line-height: 36px;
    }

    button {
        line-height: normal;
        display: block;
        width: 100%;
        text-align: center;
        padding: 0px 30px;
        min-height: 82px;
    }
}
#page_wrap{
    #aboutpage{
    }
}