
// Common Section
.about_business {
    .improve-business {
        padding: 50px 0px 30px;
    }

    .section-body .title {
        margin-bottom: 14px;
    }

    .section-body {
        .title h2 {
            text-align: left;
            margin-top: 0px;
            margin-bottom: 0px;
            font-size: 39px;
            line-height: 45px;
        }

        .free-info {
            display: inline-block;
            padding: 4px 20px;
            margin-bottom: 17px;
        }
    }

    .improve-business {
        .book_wrap {
            position: relative;
        }

        img {
            left: 0px;
            top: -225px;
            margin-bottom: 0px;
        }

        font-family: 'Open Sans',
        sans-serif;

        .row {
            margin-bottom: 0px;
        }

        p {
            margin-bottom: 20px;
            max-width: 514px;
            font-size: 18px;
        }

        form {
            .row {
                margin-left: -5px;
                margin-right: -5px;
            }

            .col-lg-6 {
                padding-left: 5px;
                padding-right: 5px;
            }

            .form-group {
                margin-bottom: 10px;
            }
        }
    }
}